@import url('https://fonts.googleapis.com/css2?family=Bigelow+Rules&family=Creepster&family=Open+Sans:wght@300&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin-top: 3rem;
  background-color: #d9afd9;
  background-image: linear-gradient(0deg, #d9afd9 0%, #97d9e1 100%);
  text-align: center;
  display: grid;
  place-items: center;

  // font-size: 1rem;

}
.App{
  text-align: center;
  width: 80vw;

}

.App h1{
  font-family: 'Creepster', cursive;
  letter-spacing: 0.3rem;
  color: rgba(219, 3, 3, 0.582);
  font-size: 3rem;
  margin-bottom: 2rem;
}

@media (max-width:400px){
  .App h1{
    font-size: 2rem;
  }
}




